import React, { useEffect } from 'react'
import { Card, Button, CardGroup} from 'react-bootstrap'
import Abogado from '../assets/Cards/Card4_00000.jpg'
import kickboxing from '../assets/kickboxing rivera.jpg'
import img3 from '../assets/laportugalia.jpg'
import './CardsBorder.css'
import { gsap } from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { BsWhatsapp } from 'react-icons/bs'

gsap.registerPlugin(ScrollToPlugin, ScrollTrigger);

const CardsBorder = () => {
    useEffect(() => {
        const CardBorder = document.querySelectorAll('.CardBorder')
        gsap.from(CardBorder, 
            {
                opacity: 0, 
                x:-100,
                delay: 0.6,
                stagger: 0.3, 
                duration: 2,  
                scrollTrigger: {
                    trigger: CardBorder,
                    start: "top center",
                    end: "bottom center", 
                }});
    }, [])
    return (
        <CardGroup>
            <Card className="CardBorder">
                <Card.Img className="CardBorder" variant="top" src={Abogado} />
                <Card.Body>
                    <Card.Title className="CardBorder">Abogado</Card.Title>
                    <Card.Text className="CardBorder">
                        Servicios generales
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <Button className="CardBorder" style={{ height: '5rem', width: '5rem'}} variant="primary">Click</Button>
                </Card.Footer>
            </Card>
            <Card className="CardBorder">
                <Card.Img className="CardBorder" variant="top" src={kickboxing} />
                <Card.Body>
                        <Card.Title className="CardBorder">Kickboxing Rivera</Card.Title>
                    <Card.Text className="CardBorder">
                            
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                <Button className="CardBorder" href="https://wa.link/m9cwdp" style={{ height: '3rem', width: '8rem'}} variant="success"><BsWhatsapp /> WhatsApp</Button>
                </Card.Footer>
            </Card>
            <Card className="CardBorder">
                <Card.Img className="CardBorder" variant="top" src={img3} />
                <Card.Body>
                    <Card.Title className="CardBorder">Pizzería La Portugalia</Card.Title>
                    <Card.Text className="CardBorder">
                        Te la llevamos calentita a domicilio. 
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <Button className="CardBorder" href="https://wa.link/qd85ib" style={{ height: '3rem', width: '8rem'}} variant="success"><BsWhatsapp />Whatsapp</Button>
                </Card.Footer>
            </Card>
        </CardGroup>
    )
}

export default CardsBorder
