import React, { useEffect } from 'react'
import { Card, Button, CardGroup} from 'react-bootstrap'
import Abogado from '../assets/Cards/Card4_00000.jpg'
import img2 from '../assets/Cards/Card3_00000.jpg'
import img3 from '../assets/Cards/Card5_00000.jpg'
import './CardsBorder.css'
import { gsap } from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { BsWhatsapp } from 'react-icons/bs'

gsap.registerPlugin(ScrollToPlugin, ScrollTrigger);

const CardsBorder_2 = () => {
    useEffect(() => {
        const CardsBorder_2 = document.querySelectorAll('.CardsBorder_2')
        gsap.from(CardsBorder_2, 
            {
                opacity: 0, 
                x:-100,
                delay: 0.6,
                stagger: 0.3, 
                duration: 2,  
                scrollTrigger: {
                    trigger: CardsBorder_2,
                    start: "top center",
                    end: "bottom center", 
                }});
    }, [])
    return (
        <CardGroup>
            <Card className="CardsBorder_2">
                <Card.Img className="CardsBorder_2" variant="top" src={Abogado} />
                <Card.Body>
                    <Card.Title className="CardsBorder_2">Nombre de tu negocio</Card.Title>
                    <Card.Text className="CardsBorder_2">
                        Servicios generales
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <Button className="CardsBorder_2" href="#" style={{ height: '3rem', width: '8rem'}} variant="success"><BsWhatsapp /> WhatsApp</Button>
                </Card.Footer>
            </Card>
            <Card className="CardsBorder_2">
                <Card.Img className="CardsBorder_2" variant="top" src={img2} />
                <Card.Body>
                        <Card.Title className="CardsBorder_2">Nombre de tu negocio</Card.Title>
                    <Card.Text className="CardsBorder_2">
                            
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <Button className="CardsBorder_2" href="#" style={{ height: '3rem', width: '8rem'}} variant="success"><BsWhatsapp /> WhatsApp</Button>
                </Card.Footer>
            </Card>
            <Card className="CardsBorder_2">
                <Card.Img className="CardsBorder_2" variant="top" src={img3} />
                <Card.Body>
                    <Card.Title className="CardsBorder_2">Nombre de tu negocio</Card.Title>
                    <Card.Text className="CardsBorder_2">
                        Descripción. 
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <Button className="CardsBorder_2" href="#" style={{ height: '3rem', width: '8rem'}} variant="success"><BsWhatsapp />Whatsapp</Button>
                </Card.Footer>
            </Card>
        </CardGroup>
    )
}

export default CardsBorder_2