import React, { useEffect } from 'react';
import { Button, Carousel } from 'react-bootstrap'
import ej1 from '../assets/fmslide.jpg'
import ej2 from '../assets/duagencia.jpg'
import ej3 from '../assets/Explora.jpg'
import { gsap } from 'gsap'
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { BsWhatsapp } from 'react-icons/bs';

gsap.registerPlugin(ScrollToPlugin, ScrollTrigger);  

const Hero = () => {

    useEffect(() => {
        const carousel = document.querySelectorAll('.carousel')
        gsap.from(carousel, {opacity: 0, x: +100, duration: 1, stagger: 0.3});
    }, [])

    return (
        <Carousel className="carousel">
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={ej1}
                alt="First slide"
                />
                <Carousel.Caption>
                    <Button className="Group btn" href="https://wa.link/yoc6mi" style={{ height: '2rem', width: '2rem', borderRadius: '50px'}} variant="success" ><BsWhatsapp /></Button>
                </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className="d-block w-100"
                    src={ej2}
                    alt="Second slide"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className="d-block w-100"
                    src={ej3}
                    alt="Third slide"
                />
                <Carousel.Caption>
                    
                    <Button className="Group btn" href="https://walink.co/68fd50" style={{ height: '2rem', width: '2rem', borderRadius: '50px', justifyContent: 'center'}} variant="success" ><BsWhatsapp /></Button>
                </Carousel.Caption>
            </Carousel.Item>
        </Carousel>
    )
}

export default Hero

