import React from 'react'
import { Card, Button} from 'react-bootstrap'
import { BsWhatsapp } from 'react-icons/bs'
import Banner2 from '../assets/Video horizontal spot1_1.mp4'

const Hor22 = () => {
  return (

      <div style={{background: '#07AD6A', alignItems: 'center'}}>
        <video className="videoHor" autoPlay muted loop style={{maxWidth: '100%'}}>
            <source src={Banner2} type="video/mp4" ></source>
        </video>
        <Button d-grid gap-2 style={{ height: '3rem', width: '3rem', borderRadius: '50px',}} variant="success"><BsWhatsapp /></Button>
      </div>
    
  )
}

export default Hor22





