import React from 'react'
import { Card, Button} from 'react-bootstrap'
import { BsWhatsapp } from 'react-icons/bs'
import Banner2 from '../assets/Comp 1/banner2_00000.jpg'

const Hor22 = () => {
  return (

        <Card className="bg-dark text-white">
          <Card.Img src={Banner2} alt="Card image" />
          <Card.ImgOverlay>
            <Card.Title></Card.Title>
              <Card.Text>
              </Card.Text>
              <Card.Text>
              </Card.Text>
            <Button style={{ height: '3rem', width: '3rem', borderRadius: '50px',}} variant="success"><BsWhatsapp /></Button>
          </Card.ImgOverlay>
        </Card>
    
  )
}

export default Hor22





